import React from 'react'

export function startTime() {
    var together = new Date();
    together.setFullYear(2020, 10, 11); 			//时间年月日，注意月份是0-11
    together.setHours(0);						//小时	
    together.setMinutes(0);						//分钟
    together.setSeconds(0);						//秒前一位
    together.setMilliseconds(0);


    timeElapse(together);


    var t = '';

    t = setTimeout(function () {
        startTime();
    }, 1000);
}



function timeElapse(date) {
    var current = new Date();
    var seconds = (Date.parse(current) - Date.parse(date)) / 1000;
    var days = Math.floor(seconds / (3600 * 24));
    seconds = seconds % (3600 * 24);
    var hours = Math.floor(seconds / 3600);
    if (hours < 10) {
        hours = "0" + hours;
    }
    seconds = seconds % 3600;
    var minutes = Math.floor(seconds / 60);
    if (minutes < 10) {
        minutes = "0" + minutes;
    }
    seconds = seconds % 60;
    if (seconds < 10) {
        seconds = "0" + seconds;
    }

    var years = 0;
    if(current.getMonth()>date.getMonth()){
        var years = Math.floor(current.getFullYear() - date.getFullYear());
    }
    if(current.getMonth()==date.getMonth()){
        if(current.getDate()>date.getDate()){
            years = 1;
        }
    }
    



    document.getElementById("sitetime")
        &&
        (document.getElementById("sitetime").innerHTML = `本站已运行 ${years} 年 ${days} 天 ${hours} 小时 ${minutes} 分钟 ${seconds} 秒`)


}


