const handlescroll = () => {
  var changgear = document.getElementById("changgear")
  let top = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset;

  var topbar = document.getElementById("nprogress").querySelector(".bar");
  var docuH = document.documentElement.clientHeight;
  var allbodyH = document.documentElement.offsetHeight;


  if (docuH && allbodyH) {
    var bardata = top / (allbodyH - docuH) * 100
    top >= 0 && (topbar.style.width = "" + bardata + "%");
  }



  var mobtop = document.getElementById("totop");
  var pctop = document.getElementById("pc-top");
  if (top >= 180) {
    mobtop && (mobtop.style.display = "block")
    pctop && (pctop.style.top = "-10vh")
  } else {
    mobtop && (document.getElementById("totop").style.display = "none")
    pctop && (pctop.style.top = "-100vh")
  }

  if(top>=40){
    changgear.style.bottom="0px"
  }else{
    changgear.style.bottom="-9999px"
  }
}


var timeOutFn = false

window.addEventListener("scroll", () => {
  timeOutFn && clearTimeout(timeOutFn);

  timeOutFn = setTimeout(function () {
    //todo 
    handlescroll()
  }, 150)


})



