var themeConfig = {};



var changeBg = (tagClass, callback) => {
  var bgAttr = bgConfig[tagClass]||tagClass;
  if (!bgAttr)
      return;
  themeConfig.bgAttr = bgAttr;

  document.body.style.backgroundImage=!bgAttr["url"]? "none" : "url(" + bgAttr["url"] + ")"

  changeSkinSecter()
  //changeSkinSecter();
  if(!callback || typeof callback == "undefined" || callback == undefined){
    return false
  }else{
    callback(bgAttr["isNight"])
  }
};

if(!Cookies.get("bgconfig")){
  changeBg(bgConfig["bg_7"])
}

if(Cookies.get("bgconfig")){
  changeBg(JSON.parse(Cookies.get("bgconfig")))
}

function changeSkinSecter(){
  if (Object.getOwnPropertyNames(themeConfig).length == 0) {
    return
  }
  var bgAttr = themeConfig.bgAttr;
  if (bgAttr["isNight"]) {
    document.documentElement.classList.add("dark-mode")//"#31363b"
    document.documentElement.classList.remove("light-mode")
  }else{
    document.documentElement.classList.add("light-mode")//"#31363b"
    document.documentElement.classList.remove("dark-mode")
  }
}



Object.keys(bgConfig).forEach(function(currBg) {
  document.querySelector(".skin-menu").querySelector("#"+currBg).onclick=()=>{
    changeBg(currBg, function() {
      Cookies.set("bgconfig", bgConfig[currBg]);
      document.querySelector(".skin-menu").classList.remove("show")
      setTimeout(function() {
        document.querySelector(".changeSkin-gear").style.visibility= "visible"
      }, 300)
  })
  }
 
})







document.getElementById("changgear").onclick=()=>{
   var menu = document.querySelector(".skin-menu")
   if(Array.from(menu.classList).indexOf("show")>=0){
    document.querySelector(".skin-menu").classList.remove("show")

   }else{
    document.querySelector(".skin-menu").classList.add("show")
   }
}

